.item {
  display: flex;
  align-items: center;
  border: 1px solid grey;
  padding: 10px;
  width: 300px;
  border-radius: 15px;
  box-shadow: 0 0 20px rgba(55%, 55%, 55%, 0.5);
  margin-bottom: 10px;
}

.span {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin-right: 10px;
}

.online {
  composes: span;
  background-color: green;
}

.offline {
  composes: span;
  background-color: red;
}

.avatar {
  border-radius: 4px;
  margin-right: 10px;
}
